import "./styles.css";
import { useState, useEffect, useRef } from "react";
import axios from "axios";

import {  EmojioneV4 } from "react-emoji-render";




function ChatMessage({ mseg }) {

  const [ userData, setUserData ] = useState(null);
  
  useEffect(() => {
    axios.get(`https://youinroll.com/profile/${mseg.user_id}/info?api=v1.1`)
              .then((resp) => {
                  let uData = resp.data.response;
                //  console.log(resp);
                  let avaurl = "";
                  if (uData.avatar.startsWith('https')){
                      avaurl = uData.avatar;
                  }else if (uData.avatar.startsWith('uploads')){
                      avaurl = 'https://react.semantic-ui.com/images/wireframe/square-image.png';
                  }
                  else{
                      avaurl = "https://youinroll.com/" + uData.avatar;
                  };
                  uData.avatar = avaurl; 
                  
                 // console.log(uData);
                  setUserData(uData);                
              })
              .catch((err) => console.log(err))
  },[]);

  return (
    <div className="chatMsg">
      <span className="authorSpan"> 
        {userData && <img  className="chatMsgImg" src={userData.avatar} alt={userData.name} />}
        <span>
          {userData && <b className="userNamedec">{`${userData.name.trim()}: `}</b> }
          <EmojioneV4 svg text={mseg.message} />
        </span>
      </span>      
    </div>
  );
}

export default function ChatMain({ toscroll }) {
  const [msgs, setMsgs] = useState([]); // page messages
  const [offset, setOffSet] = useState(0);
  const [ toRebuild, setToRebuild ] = useState(false);
  const myRef = useRef(null);
  // const executeScroll = () => myRef.current.scrollIntoView() 
  const scrollChatMain = () => {
    let objDiv = document.getElementById("chatmsgsall");
    objDiv.scrollTop = objDiv.scrollHeight;
  };

 // const stream_id = 123;
  const stream_id = window.location.href.split('/')[4];


  
  async function getRecent() { 
    console.log('in getrecent');
    axios.get(`https://youinroll.com/listen/stream?api=1.0&stream_id=${stream_id}&ts=${offset}`)
      .then((response) => { 
        console.log(response); 
        if (response.data && response.data.response === "timeout"){
            console.log("timeout")  
        } else if (response.data && response.data.response && Array.isArray([response.data.response])) {
          console.log("Is array");
          let newMsgs = response.data.response.slice(0, parseInt(response.data.response[0].id) - offset);
          setMsgs([...msgs, ...newMsgs.reverse()] );
          setOffSet(parseInt(response.data.response[0].id))          
          toscroll && scrollChatMain();
        }        
        setToRebuild(!toRebuild);
      })
      .catch((er) => {
        console.log(er);
        setToRebuild(!toRebuild);
      })
  }
   

  useEffect(() => {
    console.log('in useeffect chatmain');
    if (offset === 0 && msgs.length === 0) // if offset is 0 and there are no messages on page got to getAll endpoint
    {
      let firstMessage = {
        user_id: 456,
        message: 'Добро пожаловать в комнату прямого эфира в YouinRoll! Общайтесь с ведущим и зрителями и не забывайте соблюдать правила сообщества, чтобы эта трансляция была безопасной для просмотра.'
      };
      axios 
        .get( `https://youinroll.com/message/getAll?api=1.0&peer_id=s${stream_id}`
        // `https://youinroll.com/listen/stream?api=1.0&stream_id=s${stream_id}&access_token=${access_token}`
        )
        .then((response) => {  
          
          if (response.data.response.length > 0){           
            setOffSet(parseInt(response.data.response[0].id));
            let newMsgs = response.data.response.slice(0, parseInt(response.data.response[0].id) - offset);
            setMsgs([...msgs, ...newMsgs.reverse(), firstMessage] );                   
            //console.log(response.data.response[0].id)
          } else {
            setMsgs([firstMessage] );
            getRecent();
          }
          setToRebuild(!toRebuild);
          toscroll && scrollChatMain();    
        // console.log(response.data.response[-1]);            
        })              
        .catch((error) => {
          console.log(error);          
        });
    }else {
      // got to listen stream endpoint if there is at least one message (for example from help)
      getRecent();
    }
     
  }, [toRebuild]);

  return (
    <>
      {msgs.map((msg) => (
        <ChatMessage key={msg.id} mseg={msg}  />
      ))}
      <div ref={myRef}></div>  
    </>
  );
}
