import React, { createContext, useState, useEffect } from "react";
import axios from "axios";
import Cookies from 'js-cookie';

export const UserContext = createContext();

export default function UserContextProvider(props){



    const [ userid, setUser ] = useState(null);  // localStorage.getItem('loggeduserid') || ""
    const [ userdata, setUserData ] = useState("");
    const [ token, setToken ] = useState("");

    const phpsession = Cookies.get('PHPSESSID');

    const all_cooks = Cookies.get();

    

    
  
    useEffect(() => { 

        /* for (const property in all_cooks) {
            console.log(`${property}: ${all_cooks[property]}`);
            property[0] !== '_' && property !== 'PHPSESSID' && property.length > 8 && setUser(all_cooks[property]);
        } */

        if (!token)
            {axios
                .get("https://youinroll.com/lib/ajax/getToken.php",
                        { 
                        withCredentials: true,
                        headers: { Cookie: `PHPSESSID=${phpsession};`}
                    })
                .then((res) => {
                    console.log(res)
                    res && res.data && setToken(res.data.token);
                   // res && res.data && res.data.token && localStorage.setItem('token', res.data.token);
                })
                .catch((e) =>  console.log(e)); 
            }
        
        
        if (userid){
            localStorage.setItem('loggeduserid', userid)        
            axios.get(`https://youinroll.com/profile/${userid}/info?api=v1.1`)
            .then((resp) => {
                let uData = resp.data.response;
                let avaurl = "";
                if (uData.avatar.startsWith('https')){
                    avaurl = uData.avatar;
                }else if (uData.avatar.startsWith('uploads')){
                    avaurl = 'https://react.semantic-ui.com/images/wireframe/square-image.png';
                }
                else{
                    avaurl = "https://youinroll.com/" + uData.avatar;
                };
                uData.avatar = avaurl;                
                setUserData(uData);                
            })
            .catch((err) => console.log(err))
        }
    }, [userid, phpsession, all_cooks])
    
    return(
        <UserContext.Provider value={{user: userid, token, userdata, setUser}}>
            {props.children}
        </UserContext.Provider>
    );
}